@import '../../styles/utils/variables.scss';
@import '../../styles/utils/mixins/screenWidth.scss';
@import '../../styles/utils/mixins/helpers.scss';
@import '../../styles/utils/mixins/typography.scss';

.phones {
  &__title {
    font-family: 'Mont', sans-serif;
    font-weight: 800;
    font-size: 32px;
    line-height: 56px;
    margin-bottom: 8px;

    @include onTablet {
      font-size: 48px;
    }
  }

  &__selects {
    display: flex;
    gap: 16px;
  }

  &__select {
    margin-bottom: 24px;
  }

  &__arrow {
    position: relative;
    bottom: 2px;
  }

  &__sort-type {
    @include buttonsTypography;
    color: $c-primary;
  }

  &__select-field {
    display: flex;
    justify-content: space-between;
    width: 136px;
    height: 40px;
    border: 1px solid $c-icons;
    padding: 8px 10px;
    cursor: pointer;
    position: relative;
    background-color: $c-white;

    &--sort {
      @include onTablet {
        width: 176px;
      }
    }
  }

  &__option {
    @include buttonsTypography;
    color: $c-secondary;

    &:hover {
      color: $c-primary;
    }
  }

  &__options {
    position: absolute;

    &--hidden {
      visibility: hidden;
    }
  }

  &__select-title {
    @include smallTextTypography;
    color: $c-secondary;
    margin-bottom: 4px;
  }

  &__models-count {
    @include buttonsTypography;
    margin-bottom: 40px;
  }

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__list {
    padding: 0;
    list-style: none;
    grid-template-columns: 1fr;
    @include gridForPhones;
  }
}
